.about {
  padding: 6rem 0;
  background: #f8fafc;
}

.section-header {
  text-align: center;
  margin-bottom: 4rem;
}

.section-header h2 {
  font-size: 2.5rem;
  font-weight: 700;
  color: #1f2937;
  margin-bottom: 1rem;
}

.section-header p {
  font-size: 1.1rem;
  color: #6b7280;
}

.about-content {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 4rem;
  align-items: center;
}

.about-text h3 {
  font-size: 2rem;
  font-weight: 600;
  color: #1f2937;
  margin-bottom: 1.5rem;
}

.about-text p {
  font-size: 1.1rem;
  line-height: 1.7;
  color: #4b5563;
  margin-bottom: 1.5rem;
}

.about-stats {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  margin-top: 3rem;
}

.stat {
  text-align: center;
  padding: 1.5rem;
  background: white;
  border-radius: 1rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
  border: 1px solid #e5e7eb;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.stat::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(37, 99, 235, 0.1), transparent);
  transition: left 0.5s;
}

.stat:hover::before {
  left: 100%;
}

.stat:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(37, 99, 235, 0.15);
  border-color: #2563eb;
}

.stat h4 {
  font-size: 2rem;
  font-weight: 700;
  color: #2563eb;
  margin-bottom: 0.5rem;
  transition: all 0.3s ease;
}

.stat:hover h4 {
  transform: scale(1.1);
  color: #1d4ed8;
}

.stat p {
  color: #6b7280;
  font-weight: 500;
  margin: 0;
  transition: color 0.3s ease;
}

.stat:hover p {
  color: #374151;
}

.about-image {
  display: flex;
  justify-content: center;
  align-items: center;
}

.about-profile-container {
  position: relative;
  width: 300px;
  height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.about-profile-image {
  width: 250px;
  height: 250px;
  border-radius: 1.5rem;
  overflow: hidden;
  position: relative;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.2);
  border: 4px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
}

.about-profile-image:hover {
  transform: scale(1.05);
  box-shadow: 0 30px 60px rgba(0, 0, 0, 0.3);
}

.about-profile-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center top;
  transition: all 0.3s ease;
  filter: brightness(1.1) contrast(1.1);
}

.about-profile-image:hover img {
  transform: scale(1.1);
}

.tech-icons-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  padding: 1rem;
  pointer-events: none;
}

.tech-icon {
  font-size: 2rem;
  animation: float 3s ease-in-out infinite;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(10px);
}

.tech-icon:nth-child(1) { 
  animation-delay: 0s; 
  position: absolute;
  top: 10px;
  left: 10px;
}
.tech-icon:nth-child(2) { 
  animation-delay: 0.5s; 
  position: absolute;
  top: 10px;
  right: 10px;
}
.tech-icon:nth-child(3) { 
  animation-delay: 1s; 
  position: absolute;
  bottom: 10px;
  left: 10px;
}
.tech-icon:nth-child(4) { 
  animation-delay: 1.5s; 
  position: absolute;
  bottom: 10px;
  right: 10px;
}

@keyframes float {
  0%, 100% { transform: translateY(0px) scale(1); }
  50% { transform: translateY(-10px) scale(1.1); }
}

@media (max-width: 768px) {
  .about {
    padding: 4rem 0;
  }
  
  .about-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  
  .about-stats {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .about-profile-container {
    width: 250px;
    height: 250px;
  }
  
  .about-profile-image {
    width: 200px;
    height: 200px;
  }
  
  .tech-icon {
    font-size: 1.5rem;
    width: 40px;
    height: 40px;
  }
}
