.education {
  padding: 6rem 0;
  background: white;
}

.education-timeline {
  position: relative;
  max-width: 800px;
  margin: 0 auto;
}

.education-timeline::before {
  content: '';
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 2px;
  background: linear-gradient(to bottom, #2563eb, #7c3aed);
  transform: translateX(-50%);
}

.timeline-item {
  position: relative;
  margin-bottom: 3rem;
  width: 50%;
}

.timeline-item.left {
  left: 0;
  padding-right: 3rem;
}

.timeline-item.right {
  left: 50%;
  padding-left: 3rem;
}

.timeline-content {
  background: white;
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  border: 1px solid #e5e7eb;
  position: relative;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  overflow: hidden;
}

.timeline-content::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(37, 99, 235, 0.05), transparent);
  transition: left 0.5s;
}

.timeline-content:hover::before {
  left: 100%;
}

.timeline-content:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.2);
  border-color: #2563eb;
}

.timeline-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
}

.timeline-header h3 {
  font-size: 1.3rem;
  font-weight: 600;
  color: #1f2937;
  margin: 0;
  flex: 1;
}

.status {
  padding: 0.25rem 0.75rem;
  border-radius: 1rem;
  font-size: 0.8rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.status.ongoing {
  background: #dbeafe;
  color: #2563eb;
}

.status.completed {
  background: #dcfce7;
  color: #16a34a;
}

.institution {
  font-size: 1.1rem;
  font-weight: 600;
  color: #2563eb;
  margin-bottom: 0.5rem;
}

.location {
  color: #6b7280;
  font-size: 0.9rem;
  margin-bottom: 0.5rem;
}

.period {
  color: #374151;
  font-weight: 500;
  margin-bottom: 1rem;
}

.score {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.score-label {
  color: #6b7280;
  font-size: 0.9rem;
}

.score-value {
  background: #fbbf24;
  color: #1f2937;
  padding: 0.25rem 0.75rem;
  border-radius: 0.5rem;
  font-weight: 600;
  font-size: 0.9rem;
}

.description {
  color: #4b5563;
  line-height: 1.6;
  margin: 0;
}

.timeline-marker {
  position: absolute;
  top: 2rem;
  width: 20px;
  height: 20px;
  background: #2563eb;
  border: 4px solid white;
  border-radius: 50%;
  box-shadow: 0 0 0 4px #e5e7eb;
}

.timeline-item.left .timeline-marker {
  right: -10px;
}

.timeline-item.right .timeline-marker {
  left: -10px;
}

@media (max-width: 768px) {
  .education-timeline::before {
    left: 2rem;
  }
  
  .timeline-item {
    width: 100%;
    left: 0 !important;
    padding-left: 4rem !important;
    padding-right: 0 !important;
  }
  
  .timeline-marker {
    left: 1rem !important;
    right: auto !important;
  }
  
  .timeline-content {
    padding: 1.5rem;
  }
  
  .timeline-header {
    flex-direction: column;
    gap: 0.5rem;
  }
}
